.homeMid {
  display: flex;
  margin: auto auto;
}

.homeName {
  margin: auto auto;
}

.navBarBox {
  height: 70px;
  display: flex;
  background: cadetblue;
}

.navBar {
  display:flex;
  height: 100%;
  width: 100%;
}

.navLink {
  margin: auto 20px;
}



.midSect {
  background-image: url("./assets/images/homePageBackground.jpg");
  background-repeat: repeat;
  background-size: auto;
  height: 700px;
  overflow:auto;
}

.bottom {
  text-align: center;
  color: #ffffff;
  height: 60px;
  width: 100%;
  background: #666666;
  border-top: 5px solid #4aaaa5;
  bottom: 0px;
}

.footerText {
  text-align: center;
}

.aboutImage {
  float: left;
}

.aboutPara {
  font-size: 25px;
}

.midHeader {
  padding-left: 40px;
  color: cadetblue;
}

.workBox {
  border: 2px solid black;
  border-radius: 5px;
}

.workIconLink {
  height: 120px;
  width: 100px;
  padding: 0;
}

.workIcon {
  height: 100%;
  width: 100%;
  background: grey;
  color: white;
  text-align: center;
  display: flex;
}

.workIconText {
  margin: auto auto;
}